const domain = "https://weaverbird.dev.bilabasia.com";

export const formatImageUrl = (url: string): string => {
  if (!url) return "";
  if (url.includes("http")) return url;
  if (!/^\//.test(url)) return domain + "/" + url;
  return domain + url;
};
